import { Typography } from "@mui/material";
import { Clock, Send } from "Assets/Icons";
import {
  ChatFooterEnd,
  ChatFooterStart,
  ChatInfoFooter,
  FooterDescription,
  FooterHeading,
  LabeledIconButton,
} from "Containers/Engage/Chat/LayoutComponents";

const ChatInfo = () => {
  return (
    <ChatInfoFooter>
      <ChatFooterStart>
        <FooterHeading>
          <Clock />
          <Typography component="h4">24 Hour Window Elapsed</Typography>
        </FooterHeading>
        <FooterDescription>
          <Typography component="p">
            You can initiate conversation by sending a template message or wait
            for the user to reply.
          </Typography>
        </FooterDescription>
      </ChatFooterStart>
      <ChatFooterEnd>
        <LabeledIconButton
          label="Send Template"
          icon={<Send sx={{ height: 14, width: 14 }} />}
          sx={{ columnGap: 1.25 }}
        />
        <FooterDescription>
          <Typography component="p">
            Charges applicable as per your plan.
          </Typography>
        </FooterDescription>
      </ChatFooterEnd>
    </ChatInfoFooter>
  );
};

export default ChatInfo;
