import { Fragment } from "react";
import { Box, Collapse } from "@mui/material";

import { useChatData } from "Contexts/Engage";

const More = () => {
  const { showMore } = useChatData();

  return (
    <Fragment>
      <Collapse
        in={showMore}
        orientation="horizontal"
        sx={{
          bgcolor: "#FFFFFF",
          height: "calc(100% + 64px)",
          position: "absolute",
          right: "100%",
          bottom: 0,
          zIndex: (theme) => theme.zIndex.drawer + 2,
        }}
      >
        <Box
          height="100%"
          width={{ md: 256, xl: 400 }}
          sx={{
            borderLeft: 1,
            borderLeftColor: "#0000000D",
            transition: "width 0.2s ease-in-out",
          }}
        >
          <Box
            alignItems="center"
            display="flex"
            flexDirection="column"
            height="100%"
            justifyContent="center"
          >
            More Info
          </Box>
        </Box>
      </Collapse>
    </Fragment>
  );
};

export default More;
