import { Menu, MenuItem } from "@mui/material";

import { anchorOrigin, transformOrigin } from "Containers/App/LayoutComponents";
import { LeadOptionsSX } from "Containers/Engage/Chat/LayoutComponents";

type LeadOptionsProps = {
  open: boolean;
  anchorEl: null | HTMLElement;
  doClose: () => void;
};
const LeadOptions = ({ open, anchorEl, doClose }: LeadOptionsProps) => {
  return (
    <Menu
      anchorEl={anchorEl}
      open={open}
      onClose={doClose}
      onClick={doClose}
      keepMounted={false}
      MenuListProps={{
        "aria-labelledby": "Options",
      }}
      anchorOrigin={anchorOrigin}
      transformOrigin={transformOrigin}
      sx={LeadOptionsSX}
    >
      <MenuItem>{"+ Add a Tab"}</MenuItem>
      <MenuItem>{"Manage Tabs"}</MenuItem>
    </Menu>
  );
};

export default LeadOptions;
