import {
  Box,
  Checkbox,
  Chip,
  ListItem,
  ListItemButton,
  Typography,
} from "@mui/material";
import ReactHtmlParser from "html-react-parser";

import {
  ChatBot,
  ClockAlt,
  ClockBox,
  Star,
  Stars,
  Tag,
  User,
} from "Assets/Icons";
import { useChatApi, useChatData } from "Contexts/Engage";
import { LeadItemSx } from "Containers/Engage/Chat/LayoutComponents";
import { LeadDataSet } from "Shared/Interfaces/Engage/Chat";

import Interactions from "./Interactions";

type LeadItemProps = {
  data: LeadDataSet;
  index: number;
  checked: number[];
  doCheck: (value: number) => () => void;
};

const LeadItem = ({ data, index, checked, doCheck }: LeadItemProps) => {
  const { activeLead } = useChatData();
  const { setActiveLead } = useChatApi();
  let leadName = data.lead_data.name;

  const LeadName =
    leadName && leadName.length > 13
      ? leadName.slice(0, 13) + ".."
      : leadName || `+${data.contact_number}`;

  const PillName =
    data.assigned_user &&
    ((data.assigned_user.name &&
      data.assigned_user.name.length > 6 &&
      data.assigned_user.name.slice(0, 6) + "..") ||
      data.assigned_user.name);

  const text = data.last_message?.message
    ? data.last_message.message.text ||
      data.last_message.message.caption ||
      data.last_message.message.name
    : null;

  return (
    <ListItem
      secondaryAction={
        <Checkbox
          edge="end"
          onChange={doCheck(index)}
          checked={checked.indexOf(index) !== -1}
        />
      }
      disablePadding
      sx={LeadItemSx}
    >
      <ListItemButton
        onClick={() => setActiveLead(data.id)}
        className={activeLead && activeLead === data.id ? "active" : ""}
      >
        <Box className="above">
          <Box className="above-start">
            <Typography
              component="span"
              sx={{
                fontSize: 16,
                fontWeight: 500,
                lineHeight: "13px",
              }}
            >
              {LeadName}
            </Typography>
            {data.assigned_user && (
              <Chip
                label={PillName}
                avatar={<User sx={{ width: 9, height: 9 }} />}
                size="small"
                className="assigned-user"
              />
            )}
            {index % 2 === 0 && <Star className="status-icon" />}
            {index % 2 === 0 && <Stars className="gold-stars" />}
            {index % 2 === 1 && <Star className="gold-star" />}
            {index % 2 === 1 && <Tag className="status-icon" />}
            {text && text.length > 0 && data.ar_status && (
              <ChatBot className="status-icon" />
            )}
          </Box>
          <Box className="above-end">
            <Typography
              component="span"
              sx={{
                color: "#24272C80",
                fontSize: 10,
                fontWeight: 500,
                lineHeight: "12px",
              }}
            >
              {index < 2 ? "22/08/2021" : "No Chats"}
            </Typography>
          </Box>
        </Box>
        <Box className="below">
          <Box className="below-start">
            {text && (
              <Typography
                component="span"
                sx={{
                  color: "#24272CB3",
                  fontSize: 13,
                  fontWeight: 500,
                  lineHeight: "12px",
                }}
              >
                {ReactHtmlParser(
                  text.length > 15 ? `${text.slice(0, 15)}...` : text
                )}
              </Typography>
            )}
            <Interactions count={12} />
          </Box>
          <Box className="below-end">
            {index === 0 && (
              <Chip
                label={"RESOLVED"}
                size="small"
                sx={{
                  bgcolor: "#12B347",
                  alignItems: "center",
                  color: "#FFFFFF",
                  height: 16,
                  gap: 0.5,
                  px: 1,

                  "& .MuiChip-label": {
                    fontSize: 9,
                    fontWeight: 900,
                    lineHeight: 1,
                    px: 0,
                  },
                }}
              />
            )}
            {index === 2 && (
              <Chip
                label={"OPEN"}
                size="small"
                sx={{
                  bgcolor: "#F49134",
                  alignItems: "center",
                  color: "#FFFFFF",
                  height: 16,
                  gap: 0.5,
                  px: 1,

                  "& .MuiChip-label": {
                    fontSize: 9,
                    fontWeight: 900,
                    lineHeight: 1,
                    px: 0,
                  },
                }}
              />
            )}
            {index % 2 === 1 && (
              <Chip
                label={"11:30 AM"}
                avatar={
                  <ClockBox
                    sx={{
                      bgcolor: "#FFFFFF",
                      borderRadius: "3px",
                      color: "#12B0EE",
                      height: 16,
                      width: 16,
                      ml: -0.25,
                    }}
                  />
                }
                className="schedule"
                size="small"
                variant="outlined"
                sx={{
                  bgcolor: "transparent",
                  alignItems: "center",
                  borderColor: "#12B0EE",
                  borderWidth: 2,
                  color: "#12B0EE",
                  height: 16,
                  gap: 0.5,
                  pr: 1,

                  "& .MuiChip-label": {
                    fontSize: 9,
                    fontWeight: 900,
                    lineHeight: 1,
                    px: 0,
                  },
                }}
              />
            )}
            <ClockAlt
              sx={{
                color: index < 2 ? "#12B0EE" : "#F49134",
                height: 16,
                width: 16,
              }}
            />
          </Box>
        </Box>
      </ListItemButton>
    </ListItem>
  );
};

export default LeadItem;
