import {
  ChangeEvent,
  Fragment,
  MouseEvent,
  SyntheticEvent,
  useEffect,
  useState,
} from "react";
import { useSearchParams } from "react-router-dom";
import { Box, Checkbox, IconButton, Typography } from "@mui/material";
import {
  ArrowDropDown,
  FilterAltOutlined,
  MoreVert,
  Search,
} from "@mui/icons-material";

import { useAppState } from "Contexts/App";
import { useChatData } from "Contexts/Engage";
import {
  HeaderStartLeadingSx,
  HeaderStartTrailingSx,
} from "Containers/App/LayoutComponents";
import {
  LeadItemList,
  LeadListContent,
  LeadListData,
  LeadListHeader,
  LeadListTab,
  LeadListTabs,
  LeadListTabsSx,
  LeadListTabsWrapper,
  LeadListWrapper,
  LeadListWrapperSx,
  ListHeaderButtonSX,
  ListTabButtonSX,
} from "Containers/Engage/Chat/LayoutComponents";

import LeadOptions from "./LeadOptions";
import LeadBulkAction from "./LeadBulkAction";
import LeadItem from "./LeadItem";

const LeadList = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const { setHeaderContent } = useAppState();
  const { leadData } = useChatData();
  const [moreOptEl, setMoreOptEl] = useState<HTMLButtonElement | null>(null);
  const [bulkActionEl, setBulkActionEl] = useState<HTMLButtonElement | null>(
    null
  );
  const queryTab = searchParams.get("tab");
  const [currentTab, setCurrentTab] = useState<string>(queryTab ?? "all");
  const [checkAll, setCheckAll] = useState(false);
  const [checked, setChecked] = useState<number[]>([]);
  const openMoreOpt = Boolean(moreOptEl);
  const openBulkAction = Boolean(bulkActionEl);
  const changeTab = (_event: SyntheticEvent, newValue: string) => {
    setCurrentTab(newValue);
    setSearchParams({ tab: newValue });
  };
  const doCheckAll = (event: ChangeEvent<HTMLInputElement>) => {
    setCheckAll(event.target.checked);
    if (event.target.checked) {
      setChecked(Array.from({ length: leadData.length }, (_, i) => i));
    } else {
      setChecked([]);
    }
  };
  const doCheck = (value: number) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];
    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
    setCheckAll(false);
  };

  useEffect(() => {
    if (queryTab && queryTab !== currentTab) {
      setCurrentTab(queryTab);
    }
  }, [currentTab, queryTab]);

  useEffect(() => {
    setHeaderContent(
      <Box display="flex" alignItems="center" gap={0.5}>
        <Typography component="span" sx={HeaderStartLeadingSx}>
          33,540 Enquiries
        </Typography>
        <Typography component="span" sx={HeaderStartTrailingSx}>
          {"/"}
        </Typography>
        <Typography component="span" sx={HeaderStartTrailingSx}>
          All
        </Typography>
      </Box>
    );

    return () => {
      setHeaderContent(null);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Fragment>
      <LeadListWrapper sx={LeadListWrapperSx}>
        <LeadListTabsWrapper>
          <LeadListTabs
            value={currentTab}
            onChange={changeTab}
            variant="scrollable"
            orientation="horizontal"
            scrollButtons="auto"
            allowScrollButtonsMobile
            sx={LeadListTabsSx}
          >
            <LeadListTab label="All" value="all" />
            <LeadListTab label="Follow Up" value="follow-up" />
            <LeadListTab label="Support" value="support" />
            <LeadListTab label="Calling List" value="calling-list" />
            <LeadListTab label="Interested" value="interested" />
          </LeadListTabs>
          <IconButton
            onClick={(event: MouseEvent<HTMLButtonElement>) =>
              setMoreOptEl(event.currentTarget)
            }
            sx={ListTabButtonSX}
          >
            <ArrowDropDown />
          </IconButton>
        </LeadListTabsWrapper>
        <LeadListContent>
          <LeadListHeader>
            <Checkbox
              checked={checkAll}
              onChange={doCheckAll}
              inputProps={{ "aria-label": "Check All" }}
              sx={{
                mr: "auto",
              }}
            />
            <IconButton sx={ListHeaderButtonSX}>
              <Search />
            </IconButton>
            <IconButton sx={ListHeaderButtonSX}>
              <FilterAltOutlined />
            </IconButton>
            <IconButton
              onClick={(event: MouseEvent<HTMLButtonElement>) =>
                setBulkActionEl(event.currentTarget)
              }
              sx={ListHeaderButtonSX}
            >
              <MoreVert />
            </IconButton>
          </LeadListHeader>
          <LeadListData>
            <LeadItemList>
              {leadData.map((data, index: number) => (
                <LeadItem key={index} {...{ data, index, doCheck, checked }} />
              ))}
            </LeadItemList>
          </LeadListData>
        </LeadListContent>
      </LeadListWrapper>
      <LeadOptions
        open={openMoreOpt}
        anchorEl={moreOptEl}
        doClose={() => setMoreOptEl(null)}
      />
      <LeadBulkAction
        open={openBulkAction}
        anchorEl={bulkActionEl}
        doClose={() => setBulkActionEl(null)}
      />
    </Fragment>
  );
};

export default LeadList;
