import { Menu, MenuItem } from "@mui/material";

import { anchorOrigin, transformOrigin } from "Containers/App/LayoutComponents";
import { LeadBulkActionSX } from "Containers/Engage/Chat/LayoutComponents";

type LeadBulkActionProps = {
  open: boolean;
  anchorEl: null | HTMLElement;
  doClose: () => void;
};

const LeadBulkAction = ({ open, anchorEl, doClose }: LeadBulkActionProps) => {
  return (
    <Menu
      anchorEl={anchorEl}
      open={open}
      onClose={doClose}
      onClick={doClose}
      keepMounted={false}
      MenuListProps={{
        "aria-labelledby": "Bulk Actions",
      }}
      anchorOrigin={anchorOrigin}
      transformOrigin={transformOrigin}
      sx={LeadBulkActionSX}
    >
      <MenuItem>{"Action 1"}</MenuItem>
      <MenuItem>{"Action 2"}</MenuItem>
    </Menu>
  );
};

export default LeadBulkAction;
