export const LabeledIconButton = <C extends ElementType>(
  props: ButtonProps<C, { component?: C }> | any
) => {
  const {
    label,
    type = "button",
    color = "inherit",
    variant,
    disableIcon = false,
    iconStart = false,
    disabled,
    icon,
    sx,
    ...rest
  } = props;
  return (
    <Button
      type={type}
      color={color}
      variant={variant}
      disabled={disabled}
      sx={{
        ...(variant !== "outlined"
          ? variant !== "text"
            ? {
                ...(color === "inherit"
                  ? { ...LabeledIconButtonSX }
                  : { ...LabeledIconButtonAltSX }),
                ...sx,
              }
            : {
                ...LabeledIconButtonTextSX,
                ...sx,
              }
          : {
              ...sx,
            }),
        ...(iconStart
          ? { flexDirection: "row" }
          : { flexDirection: "row-reverse" }),
      }}
      {...rest}
    >
      {!disableIcon && icon}
      {label}
    </Button>
  );
};

