import { Menu, MenuItem } from "@mui/material";

import { useChatApi, useChatData } from "Contexts/Engage";

const AttachmentSelector = () => {
  const { currentAction, chatActionEl } = useChatData();
  const { setCurrentAction, setChatActionEl } = useChatApi();
  const doClose = () => {
    setChatActionEl(null);
    setCurrentAction("");
  };

  return (
    <Menu
      anchorEl={chatActionEl}
      open={currentAction === "attachment"}
      onClose={doClose}
      onClick={doClose}
      MenuListProps={{
        "aria-labelledby": "Account Menu",
      }}
      anchorOrigin={{
        vertical: "top",
        horizontal: "left",
      }}
      transformOrigin={{
        vertical: "bottom",
        horizontal: "left",
      }}
      sx={{
        "& .MuiPaper-root": {
          borderRadius: 1,
          boxShadow: "0px 2px 4px 0px #0000001A",
          mt: -0.5,
          // "& .MuiList-root": { mb: 0.5 },
        },
      }}
    >
      <MenuItem>{"Documents"}</MenuItem>
      <MenuItem>{"Photo or Video"}</MenuItem>
    </Menu>
  );
};

export default AttachmentSelector;
