import { Box, Typography } from "@mui/material";

import { InteractionSx } from "Containers/Engage/Chat/LayoutComponents";

type InteractionsProps = {
  count: number;
};
const Interactions = ({ count }: InteractionsProps) => {
  return (
    <Box sx={InteractionSx}>
      <Typography component="span">{count}</Typography>
    </Box>
  );
};

export default Interactions;
