import { Fragment, useState } from "react";
import {
  Avatar,
  Backdrop,
  Box,
  Collapse,
  IconButton,
  Tabs,
  Tab,
  Typography,
  Badge,
  Button,
  Chip,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from "@mui/material";
import { ExpandMore, Info } from "@mui/icons-material";

import { useGlobal } from "Contexts/App";
import { useChatApi, useChatData } from "Contexts/Engage";
import { Call, Note, Pencil, Reminder, Star, Tag, Ticket } from "Assets/Icons";
import {
  AboutInfo,
  AboutInfoHeading,
  ChatInfoInner,
  ChatInfoWrapper,
  ChatInfoWrapperSX,
  CollapsibleInfo,
  CustomersInterest,
  InfoContent,
  InfoHeader,
  InfoHeaderEnd,
  InfoHeaderStart,
  InfoTags,
  InfoTagsEnd,
  InfoTagsStart,
  NoteArea,
  SomeNotes,
  NotesContent,
  NotesHeader,
  UserInfo,
} from "Containers/Engage/Chat/LayoutComponents";
import { userInitials } from "Utils";

import More from "./More";
import AllNotes from "./Info/AllNotes";
import AddNotes from "./Info/AddNotes";

const ChatInfo = () => {
  const counter = 2;
  const { isBelowDesk } = useGlobal();
  const { showInfoTab, showMore, showAllNotes, showAddNotes } = useChatData();
  const { setShowInfoTab, setShowMore, setShowAllNotes, setShowAddNotes } =
    useChatApi();
  const [expanded, setExpanded] = useState<string | false>(false);
  const handleChange =
    (panel: string) => (event: React.SyntheticEvent, isExpanded: boolean) => {
      setExpanded(isExpanded ? panel : false);
    };
  const info = [
    { label: "Name", value: "Sameer Saxena" },
    { label: "Phone", value: "+91 454 4545 454" },
    { label: "Email", value: "sameer@aol.com" },
    { label: "Designation", value: "Customer" },
    { label: "Origin", value: "WhatsApp" },
    { label: "Created At", value: "12th Jan 2021, 4:00PM IST" },
    { label: "Last Responed", value: "12th Jan 2021, 4:03PM IST" },
    { label: "Sessions", value: "1" },
  ];
  const location = [
    { label: "Address", value: "4235+R4Q, Digdoh, Maharashtra 440016, India" },
    { label: "District", value: "Mumbai" },
    { label: "City", value: "Mumbai" },
    { label: "State", value: "Maharashtra" },
    { label: "Country", value: "India" },
    { label: "Zip Code", value: "440016" },
    { label: "Country Code", value: "IN" },
    { label: "Latitude", value: "23.34343" },
    { label: "Longitude", value: "76.54455" },
    { label: "LatLong", value: "23.34343, 76.544455" },
    { label: "Sessions", value: "1" },
  ];
  const notes = [
    {
      point:
        "Interested to discuss further, asked to call back on 3rd May, 2022.",
      added_by: "Rajesh Sharma",
      added_at: "28th April 2022 3:43 PM",
    },
    {
      point:
        "Interested to discuss further, asked to call back on 3rd May, 2022.",
      added_by: "Rajesh Sharma",
      added_at: "28th April 2022 3:43 PM",
    },
    {
      point:
        "Interested to discuss further, asked to call back on 3rd May, 2022.",
      added_by: "Rajesh Sharma",
      added_at: "28th April 2022 3:43 PM",
    },
    {
      point:
        "Interested to discuss further, asked to call back on 3rd May, 2022.",
      added_by: "Rajesh Sharma",
      added_at: "28th April 2022 3:43 PM",
    },
    {
      point:
        "Interested to discuss further, asked to call back on 3rd May, 2022.",
      added_by: "Rajesh Sharma",
      added_at: "28th April 2022 3:43 PM",
    },
    {
      point:
        "Interested to discuss further, asked to call back on 3rd May, 2022.",
      added_by: "Rajesh Sharma",
      added_at: "28th April 2022 3:43 PM",
    },
  ];

  return (
    <Fragment>
      <Backdrop
        sx={{
          backdropFilter: "blur(2px)",
          color: "#fff",
          // position: "absolute", // to span over relative parent
          zIndex: (theme) => theme.zIndex.drawer,
        }}
        open={showInfoTab}
        onClick={() => {
          setShowInfoTab(false);
          setShowMore(false);
          setShowAllNotes(false);
        }}
      />
      <Backdrop
        sx={{
          backdropFilter: { lg: "blur(2px)" },
          bgcolor: { xs: "transparent", lg: "#00000080" },
          color: "#fff",
          // position: "absolute", // to span over relative parent
          zIndex: (theme) => theme.zIndex.drawer + 1,
        }}
        open={showMore}
        onClick={() => setShowMore(false)}
      />
      <Backdrop
        sx={{
          backdropFilter: { lg: "blur(2px)" },
          bgcolor: { xs: "transparent", lg: "#00000080" },
          color: "#fff",
          // position: "absolute", // to span over relative parent
          zIndex: (theme) => theme.zIndex.drawer + 1,
        }}
        open={showAllNotes || showAddNotes}
        onClick={() => {
          setShowAllNotes(false);
          setShowAddNotes(false);
        }}
      />
      <Collapse
        in={!isBelowDesk || showInfoTab}
        orientation="horizontal"
        sx={{
          bgcolor: "#FFFFFF",
          height: "100%",
          position: { xs: "absolute", lg: "initial" },
          right: 0,
          top: 0,
          zIndex: (theme) => theme.zIndex.drawer + 2,

          "& .MuiCollapse-wrapperInner": {
            position: "relative",
          },
        }}
      >
        <ChatInfoWrapper sx={ChatInfoWrapperSX}>
          <ChatInfoInner>
            <InfoHeader>
              <InfoHeaderStart>
                <Avatar
                  {...userInitials("Parag Rudani")}
                  sx={{ width: { xs: 58, xl: 90 }, height: { xs: 58, xl: 90 } }}
                />
                <UserInfo>
                  <Box alignItems="center" display="flex" columnGap={0.5}>
                    <Typography component="h4" className="name">
                      Parag Rudani
                    </Typography>
                    <IconButton
                      sx={{ color: (theme) => theme.palette.primary.main }}
                    >
                      <Pencil />
                    </IconButton>
                  </Box>
                  <Typography component="h6" className="role">
                    Customer
                  </Typography>
                  <Typography component="p" className="contact">
                    +91 73494 34309
                  </Typography>
                </UserInfo>
              </InfoHeaderStart>
              <InfoHeaderEnd>
                <Tabs
                  orientation={"horizontal"}
                  value={"notes"}
                  variant={"fullWidth"}
                >
                  <Tab
                    // disableRipple
                    // disableFocusRipple
                    icon={<Note />}
                    label="Notes"
                    onClick={() => {
                      setShowAllNotes(false);
                      setShowAddNotes(!showAddNotes);
                    }}
                    value="notes"
                  />
                  <Tab
                    // disableRipple
                    // disableFocusRipple
                    icon={
                      <Badge
                        badgeContent={counter}
                        color="primary"
                        anchorOrigin={{
                          vertical: "top",
                          horizontal: "right",
                        }}
                      >
                        <Ticket />
                      </Badge>
                    }
                    label="Tickets"
                    onClick={() => setShowMore(!showMore)}
                    value="ticket"
                  />
                  <Tab
                    // disableRipple
                    // disableFocusRipple
                    icon={<Call />}
                    label="Call"
                    onClick={() => setShowMore(!showMore)}
                    value="call"
                  />
                  <Tab
                    // disableRipple
                    // disableFocusRipple
                    icon={
                      <Badge
                        badgeContent={counter}
                        color="primary"
                        anchorOrigin={{
                          vertical: "top",
                          horizontal: "right",
                        }}
                      >
                        <Reminder />
                      </Badge>
                    }
                    label="Reminder"
                    onClick={() => setShowMore(!showMore)}
                    value="reminder"
                  />
                </Tabs>
              </InfoHeaderEnd>
            </InfoHeader>
            <InfoContent>
              <InfoTags>
                <InfoTagsStart>
                  <Typography component="h5">Tags</Typography>
                  <Button disableRipple disableFocusRipple variant="text">
                    ADD TAGS
                  </Button>
                </InfoTagsStart>
                <InfoTagsEnd>
                  <Chip
                    icon={<Tag />}
                    size="small"
                    label="Sales"
                    sx={{ bgcolor: "#FFEBC6", color: "#DA805A" }}
                  />
                  <Chip
                    icon={<Tag />}
                    size="small"
                    label="Support"
                    sx={{ bgcolor: "#C6F8FF", color: "#5F989F" }}
                  />
                </InfoTagsEnd>
              </InfoTags>
              <AboutInfo>
                <AboutInfoHeading>About this contact</AboutInfoHeading>
                <CollapsibleInfo>
                  <CustomersInterest>
                    <Star />
                    <Typography component="p">Interested</Typography>
                    <Typography component="span">
                      {"auto profiled by EasySocial "}
                      <Info />
                    </Typography>
                  </CustomersInterest>
                  <Accordion
                    expanded={expanded === "information"}
                    onChange={handleChange("information")}
                  >
                    <AccordionSummary
                      expandIcon={<ExpandMore />}
                      aria-controls="information-content"
                      id="information-header"
                    >
                      <Typography>Information</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                      {info.map(
                        (
                          { label, value }: { label: string; value: string },
                          index
                        ) => (
                          <Fragment key={index}>
                            <Typography component="span">{label}</Typography>
                            <Typography component="h5">{value}</Typography>
                          </Fragment>
                        )
                      )}
                    </AccordionDetails>
                  </Accordion>
                  <Accordion
                    expanded={expanded === "location"}
                    onChange={handleChange("location")}
                  >
                    <AccordionSummary
                      expandIcon={<ExpandMore />}
                      aria-controls="location-content"
                      id="location-header"
                    >
                      <Typography>Location</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                      {location.map(
                        (
                          { label, value }: { label: string; value: string },
                          index
                        ) => (
                          <Fragment key={index}>
                            <Typography component="span">{label}</Typography>
                            <Typography component="h5">{value}</Typography>
                          </Fragment>
                        )
                      )}
                    </AccordionDetails>
                  </Accordion>
                </CollapsibleInfo>
              </AboutInfo>
              <SomeNotes>
                <NotesHeader>
                  <Typography component="h5">Notes</Typography>
                  <Button
                    disableRipple
                    disableFocusRipple
                    variant="text"
                    onClick={() => {
                      setShowAddNotes(false);
                      setShowAllNotes(!showAllNotes);
                    }}
                  >
                    Show All
                  </Button>
                </NotesHeader>
                <NotesContent>
                  {notes.map(
                    (
                      {
                        point,
                        added_by,
                        added_at,
                      }: { point: string; added_by: string; added_at: string },
                      index
                    ) => (
                      <NoteArea key={index}>
                        <Typography component="h5">{point}</Typography>
                        <Typography component="h6">{added_by}</Typography>
                        <Typography component="h6">{added_at}</Typography>
                      </NoteArea>
                    )
                  )}
                </NotesContent>
              </SomeNotes>
            </InfoContent>
          </ChatInfoInner>
        </ChatInfoWrapper>
        <More />
        <AllNotes />
        <AddNotes />
      </Collapse>
    </Fragment>
  );
};

export default ChatInfo;
