import { Box, ClickAwayListener, Grow, Popper } from "@mui/material";

import { useChatApi, useChatData } from "Contexts/Engage";
import EmojiPicker, { EmojiClickData, Theme } from "emoji-picker-react";

const EmojiSelector = () => {
  const { message, currentAction, chatActionEl, mainContentRef } =
    useChatData();
  const { setChatBoxValue, setCurrentAction, setChatActionEl } = useChatApi();
  const doClose = () => {
    setChatActionEl(null);
    setCurrentAction("");
  };
  const onEmojiClick = (emojiData: EmojiClickData) =>
    setChatBoxValue("message", `${message}${emojiData.emoji}`);
  return (
    <Popper
      open={currentAction === "emoji"}
      anchorEl={chatActionEl}
      placement="top-start"
      transition
    >
      {({ TransitionProps }) => (
        <Grow
          {...TransitionProps}
          timeout={350}
          style={{
            transformOrigin: "left bottom",
          }}
        >
          <Box className="wrapper" sx={{ mb: 0.5 }}>
            <ClickAwayListener onClickAway={doClose}>
              <Box className="inner">
                <EmojiPicker
                  onEmojiClick={onEmojiClick}
                  autoFocusSearch={false}
                  theme={Theme.LIGHT}
                  // height={400}
                  width={mainContentRef.current?.clientWidth - 40 ?? 450}
                />
              </Box>
            </ClickAwayListener>
          </Box>
        </Grow>
      )}
    </Popper>
  );
};

export default EmojiSelector;
