export const useChatData = () => {
  const context = useContext(ChatData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of ChatProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export const useChatApi = () => {
  const context = useContext(ChatApi);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of ChatProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default ChatProvider;
