import { Box } from "@mui/material";

import { useChatData } from "Contexts/Engage";
import { ChatMainContent } from "Containers/Engage/Chat/LayoutComponents";

import ChatInfo from "./Info";
import ChatHeader from "./MainContent/ChatHeader";
import ChatAreaComponent from "./MainContent/ChatArea";
import ChatFooter from "./MainContent/ChatFooter";

const ChatMain = () => {
  const { mainContentRef } = useChatData();

  return (
    <Box bgcolor="#F7F8FA" flexGrow={1} display="flex" position="relative">
      <ChatMainContent ref={mainContentRef}>
        <ChatHeader />
        <ChatAreaComponent />
        <ChatFooter />
      </ChatMainContent>
      <ChatInfo />
    </Box>
  );
};

export default ChatMain;
