import { ReactNode } from "react";

import { ChatProvider } from "Contexts/Engage";

import { FlexBoxFullSpaceBetween } from "./Chat/LayoutComponents";

type ChatContainerProps = {
  children: ReactNode;
};

const ChatContainer = ({ children }: ChatContainerProps) => {
  return (
    <ChatProvider>
      <FlexBoxFullSpaceBetween>{children}</FlexBoxFullSpaceBetween>
    </ChatProvider>
  );
};

export default ChatContainer;
