import { Fragment, ReactNode } from "react";
import { IconButton, ToggleButton } from "@mui/material";

import { Attach, Bolt, Bot, Emoji, GeoLocation, Send } from "Assets/Icons";
import {
  ChatBoxAbove,
  ChatBoxBelow,
  ChatBoxFooter,
  ChatBoxStart,
  LabeledIconButton,
} from "Containers/Engage/Chat/LayoutComponents";
import { useChatApi, useChatData } from "Contexts/Engage";
import FormTextField from "SharedComponents/FormComponents/FormTextField";

import ChatAction from "./ChatAction";
import { ChatBoxInterFace } from "Shared/Interfaces/Engage/Chat";

type ChatBoxAction = {
  icon: ReactNode;
  value: string;
};
const ChatBox = () => {
  const { chatBoxControl, currentAction } = useChatData();
  const { chatBoxSubmit, setCurrentAction, setChatActionEl } = useChatApi();
  const ChatBoxActions: ChatBoxAction[] = [
    { icon: <Emoji />, value: "emoji" },
    { icon: <Attach />, value: "attachment" },
    { icon: <GeoLocation />, value: "geolocation" },
    { icon: <Bolt />, value: "quick_replies" },
  ];
  const handleChange = (
    event: React.MouseEvent<HTMLElement>,
    newAlignment: string
  ) => {
    setChatActionEl(event.currentTarget);
    setCurrentAction(newAlignment);
  };
  const onSubmit = (data: ChatBoxInterFace) => {
    console.log(data);
  };

  return (
    <Fragment>
      <ChatBoxFooter>
        <ChatBoxAbove>
          <ChatBoxStart
            exclusive
            color="primary"
            value={currentAction}
            onChange={handleChange}
            aria-label="Chat box actions"
          >
            {ChatBoxActions.map(({ icon, value }, index) => (
              <ToggleButton key={index} value={value}>
                {icon}
              </ToggleButton>
            ))}
          </ChatBoxStart>
          <LabeledIconButton
            color="primary"
            disableFocusRipple
            disableRipple
            disableTouchRipple
            label="Send Template"
            icon={<Bot sx={{ height: 14, width: 14 }} />}
            iconStart
            variant="text"
          />
        </ChatBoxAbove>
        <ChatBoxBelow onSubmit={chatBoxSubmit(onSubmit)}>
          <FormTextField
            control={chatBoxControl}
            name="message"
            placeholder="Write a Message..."
            maxRows={5}
            variant="standard"
            multiline
          />
          <IconButton sx={{ color: "#12B0EE", mb: 0.75 }} type="submit">
            <Send sx={{ width: undefined, height: undefined }} />
          </IconButton>
        </ChatBoxBelow>
      </ChatBoxFooter>
      <ChatAction />
    </Fragment>
  );
};

export default ChatBox;
