import { IconButton, Typography } from "@mui/material";
import { Info } from "@mui/icons-material";

import { UserAdd } from "Assets/Icons";
import { useChatApi } from "Contexts/Engage";
import {
  ChatHeader,
  ChatHeaderEnd,
  ChatHeaderStart,
  ChatTitle,
  LabeledIconButton,
} from "Containers/Engage/Chat/LayoutComponents";

const ChatHeaderComponent = () => {
  const { setShowInfoTab } = useChatApi();
  return (
    <ChatHeader>
      <ChatHeaderStart>
        <ChatTitle>
          <Typography component="h4">{"Parag Rudani"}</Typography>
          <Typography component="span">{"via WhatsApp"}</Typography>
        </ChatTitle>
      </ChatHeaderStart>
      <ChatHeaderEnd>
        <LabeledIconButton
          label="Assign"
          icon={<UserAdd sx={{ height: 14, width: 14 }} />}
          iconStart
        />
        <IconButton
          sx={{ color: "#00000080", display: { lg: "none" } }}
          onClick={() => setShowInfoTab((showInfoTab) => !showInfoTab)}
        >
          <Info />
        </IconButton>
      </ChatHeaderEnd>
    </ChatHeader>
  );
};

export default ChatHeaderComponent;
